
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services';

export default {
components: {
Scroller,
Spinner,
XButton,
Group,
Cell,
LoadMore
},
data () {
return {
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
currentType:0,/* 0-66 */
/*  */
typeList:[{
name:"",
type:0,
},{
name:"",
type:1,
},{
name:"",
type:2,
},{
name:"",
type:3,
},{
name:"",
type:4,
},{
name:"",
type:5,
},{
name:"",
type:6,
},],
/*  */
timeMap:{1:"",2:"",3:"",4:"",5:""},
/*  */
orderList:[]
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),

},
mounted () {
if(!this.orderList[0]){
this.getTypeData();
}
},
methods: {
/* typelist */
typeselect(index){
this.currentType=index;
},
confrimType(item){
if (item.order_is_delete == 0) {
if (item.order_refund_goods_status == 0) {
if (item.order_status == 1) {
if(item.order_shipping_status == 8){
item.orderType=8;//
}else{
item.orderType=1;//
}
} else if (item.order_status == 2) {
switch (item.order_shipping_status) {
case 1:
item.orderType=2;//
break;
case 2:
item.orderType=3;//
break;
case 3:
item.orderType=4;//
break;
case 4:
item.orderType=5;//
break;
case 5:
item.orderType=5;//
item.srcorderType=2;
break;
case 6:
item.orderType=6;//
break;
case 7:
item.orderType=7;//
break;
}
}
} else if (item.order_refund_goods_status == 1) {
item.orderType=9;//
} else if (item.order_refund_goods_status == 2) {
if (item.order_status == 2) {//
if (item.order_shipping_status == 2) {
item.srcorderType=0;//
} else if (item.order_shipping_status == 3) {
item.srcorderType=0;//
} else if (item.order_shipping_status == 5) {
item.srcorderType=1;//
} else if (item.order_shipping_status == 6) {
item.orderType=10;//
}
}
} else if (item.order_refund_goods_status == 4) {
if (item.order_shipping_status == 6) {
item.orderType=10;//
}
}else if(item.order_refund_goods_status == 3){
item.srcOrderType=3;//
}
}
},
/*  */
getTypeData(){
API.order.orderlist({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderStatus:0
}).then((res)=>{
if(res.body.code==200){
let list= res.body.data.orderList.data
for(let item of list) {
this.confrimType(item);
}
this.orderList=list;
}
});
}
}
}






